import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
import api from '@/common/index.js';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import over from '@/components/over.vue'
var mvc = {
	saleList: [],
	cartoon: false,
	over: false
};
export default {
	components: {
		uniNavBar,
		uniLoadMore,
		empty,
		over
	},
	data() {
		return {

			navList: [{
					state: 0,
					text: '申报产品',
					loadingType: 'more',
					saleList: []
				},
				{
					state: 1,
					text: '寄回产品',
					loadingType: 'more',
					saleList: []
				},
			],
			mvc: {
				saleList: [],
				cartoon: false,
				over: false
			}

		};
	},
	computed: {
		i18n() {
			return this.$t('sale')
		}
	},
	onLoad(options) {
		api.pageTitle(this.$t('sale').title)




		if (options.state == 0) {
			this.loadData()
		}


	},
	onShow() {
		uni.showLoading({
			title: '正在加载'
		})
		this.loadData();
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下

			let that = this;
			mvc.over = false;
			that.$ajax.saleData().then(res => {
				mvc.saleList = res.list;
				if (mvc.saleList.length == 0) {
					mvc.cartoon = true
				} else {
					mvc.over = true;
				}
			})
			this.mvc = mvc;
			uni.hideLoading();

		},
		details(e) {
			uni.navigateTo({
				url: '/pages/sale-detail/sale-detail?id=' + e
			})

		},
		addSale(e) {
			uni.navigateTo({
				url: '/pages/sale-add/sale-add'
			})
		},
		navlogistics(e) {
			uni.navigateTo({
				url: '/pages/logistics-list/logistics-list'
			})
		}
	},
}
